.LandingPage {
  background-image: -webkit-gradient(
      linear,
      left top, right bottom,
      from(rgba(247, 247, 247, 0.8)),
      to(rgba(55, 55, 55, 0.9))
    ),
    url("../../../assets/imgs/landing.jpg");
  background-image: -webkit-linear-gradient(
      left top,
      rgba(247, 247, 247, 0.8),
      rgba(55, 55, 55, 0.9)
    ),
    url("../../../assets/imgs/landing.jpg");
  background-image: -o-linear-gradient(
      left top,
      rgba(247, 247, 247, 0.8),
      rgba(55, 55, 55, 0.9)
    ),
    url("../../../assets/imgs/landing.jpg");
  background-image: linear-gradient(
      to right bottom,
      rgba(247, 247, 247, 0.8),
      rgba(55, 55, 55, 0.9)
    ),
    url("../../../assets/imgs/landing.jpg");
  width: 100%;
  height: 100vh;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  color: black;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  text-shadow: 2px 2px 2px rgba(50, 50, 50, 1);
  position: relative;
}

.AdmBtn {
  position: absolute;
  top: 1.5rem;
  right: 1.5rem;
}

.MoveUp {
  margin-top: -10rem;
}

.LandingPage span {
  font-family: "Blanch Caps Inline Regular";
  font-size: 8rem;
}

.LandingPage .BurgerTitle {
  font-size: 11rem;
  color: #ffa726;
}

.LandingPage p {
  font-family: "Haymaker";
  font-size: 2.5rem;
  text-align: center;
  color: #ffa726;
  margin-top: -3rem;
}

.LPBtns {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  margin-top: 3rem;
}



